

















































































.ve-container {
  position: relative;
  width: calc(100% - 40px);
  margin: 0 auto;
  .ve-button-area {
    margin: 10px 0 0 0;
    .ve-button {
      padding: 4px 10px;
      background: #00a3cf;
      border: 0;
      border-radius: 3px;
      color: #fff;
      cursor: pointer;
    }
  }
}



