@charset "UTF-8";
@import url(./../../node_modules/quill/dist/quill.core.css);
@import url(./../../node_modules/quill/dist/quill.snow.css);
@import url(./../../node_modules/n-quill-emoji/dist/n-quill-emoji.css);
.ql-editor {
  min-height: 200px;
  font-size: 14px; }

.quill-wrapper .ql-disabled {
  cursor: not-allowed;
  user-select: none; }

.quill-wrapper .ql-editor ul[data-checked='true'] > li::before,
.quill-wrapper .ql-editor ul[data-checked='false'] > li::before {
  font-size: 14px;
  vertical-align: baseline;
  bottom: 0;
  font-weight: 900;
  color: #222; }

.quill-wrapper .ql-snow.ql-toolbar {
  padding-top: 8px;
  padding-bottom: 4px;
  line-height: 26px; }
  .quill-wrapper .ql-snow.ql-toolbar button svg {
    width: 18px;
    height: 18px; }
  .quill-wrapper .ql-snow.ql-toolbar .ql-formats {
    border: 1px solid #ccc;
    margin-bottom: 4px; }
    .quill-wrapper .ql-snow.ql-toolbar .ql-formats button {
      margin: 0; }
    .quill-wrapper .ql-snow.ql-toolbar .ql-formats button:not(:last-child) {
      border-right: 1px solid #ccc; }

.quill-wrapper .ql-snow .ql-picker.ql-size {
  height: 24px;
  line-height: 24px; }

.quill-wrapper .ql-snow .ql-thin, .quill-wrapper .ql-snow .ql-stroke.ql-thin {
  stroke-width: 1px !important; }

.quill-wrapper .ql-snow .ql-stroke {
  stroke: rgba(63, 63, 63, 0.95);
  stroke-linecap: square;
  stroke-linejoin: initial;
  stroke-width: 1.7px; }

.quill-wrapper .ql-snow .ql-active .ql-stroke {
  stroke-width: 2.25px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='12px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='12px']::before {
  content: '12px';
  font-size: 12px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='13px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='13px']::before {
  content: '13px';
  font-size: 13px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='16px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='16px']::before {
  content: '16px';
  font-size: 14px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='18px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='18px']::before {
  content: '18px';
  font-size: 15px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='24px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='24px']::before {
  content: '24px';
  font-size: 16px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label[data-value='32px']::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item[data-value='32px']::before {
  content: '32px';
  font-size: 18px; }

.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-label::before,
.quill-wrapper .ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: '正常';
  font-size: 14px; }

.quill-wrapper .ql-snow .ql-tooltip[data-mode='link']::before {
  content: '输入链接:'; }

.quill-wrapper .ql-snow .ql-tooltip::before {
  content: '访问链接:'; }

.quill-wrapper .ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  content: '保存'; }

.quill-wrapper .ql-snow .ql-tooltip a.ql-action::after {
  content: '编辑'; }

.quill-wrapper .ql-snow .ql-tooltip a.ql-remove::before {
  content: '移除'; }



